
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
export default {
  name: 'app',
  components: {
    Loading
  },
  computed: {
    ...mapState({
      isLoading: state => state.isLoading
    })
  },
  mounted(){
    console.log(this.isLoading);
  }
}



